// Blog

.blog__item {
  font-size: em(14);
  margin-bottom: 2em;

  @include tablet-port {
    font-size: em(16);
    margin-bottom: 3em;
  }

  @include wide {
    font-size: em(18);
    margin-bottom: 4em;
  }
}

.blog__item-image-container {
  margin-bottom: 1.5em;

  a {
    display: block;
    @include hover-border;
  }

  img {
    max-width: none;
    width: 100%;
  }
}

.blog__item-header,
.blog__item-excerpt {
  padding: 0 3px;
}

.blog__item-tag {
  color: #929292;
  display: inline-block;
  font-weight: 600;
  margin-bottom: 0.5em;
  text-transform: uppercase;

  @include hover-underline;
}

.blog__item-title {
  color: $color-base;
  font-size: em(20);
  font-weight: bold;
  margin-bottom: 1rem;

  a {
    color: inherit;

    @include hover-underline;
  }
}

.blog__item-excerpt {
  color: #494949;
  line-height: 1.9;

  p {
    margin-bottom: 1.5em;
  }
}

.blog__item-read {
  color: $color-highlight;
  display: block;
  font-weight: bold;
  text-align: right;

  @include hover-underline;

  &::after {
    content: " \00bb";
  }
}
